import React from 'react';
import { Error } from './Error';
import { connect } from 'react-redux';
import './movieStyle.css';
import { Link } from 'react-router-dom';
import '../../node_modules/bootstrap/dist/css/bootstrap.min.css';
const Card = (props) => {
    return (
        <div className='row' id='movie'>
            {
                props.fetchMovies.map(movie => {
                    if (props.status === true) {
                        return (
                            <div key={movie.imdbID} className='col-xl-4 col-lg-4 col-sm-6'>
                                 <div className="card">
                                <div id='imgOfCard'>
                                    <img src={movie.Poster} alt='not found' />
                                </div>
                                <p>{movie.Title} - {movie.Year}</p>
                                <Link to={'/MovieInfo/' + movie.imdbID} className='btn btn-dark text-light'>More Details</Link>
                            </div>
                            </div>
                             )
                    }
                    else if(props.status === false) {
                        return (
                            <div key={0}>
                                <Error/>
                            </div>
                        )
                    }
                })
            }
        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        fetchMovies: state.fetchMovies,
        status: state.status
    }
}
const featuredFn = connect(mapStateToProps);
export const MovieCard = featuredFn(Card);