import React from 'react';
import './inputStyle.css'
import '../../node_modules/bootstrap/dist/css/bootstrap.min.css';
export const Input = (props) =>{
    return(
        <div id="input">
            <i className="fas fa-search fa-2x"></i>
            <h2>Search for Movie and Tv Series...</h2>
            
        <div className="form-group">
            <input type='text' className=' form-control my-2 ' placeholder='Type here for get info' onChange={(event)=>{
                props.takeInput(event, 'text')
            }}/>
        </div>
            <button className='btn btn-dark ' onClick={props.takeOperation}>Search Now...</button>   
        </div>
    )
}