import { initialState } from '../models/globalStore';
export const rootReducer = (currentState = initialState, action) => {
    switch (action.type) {
        case 'SEARCH_MOVIE':
            return {
                ...currentState,
                status: action.payLoad.status,
                fetchMovies: action.payLoad.fetchMovies,
                inputValue: action.payLoad.searchValue,
                loading: false
            }
        case 'FETCH_MOVIE':
            return {
                ...currentState,
                movieInfoObj: action.payLoad,
                loading: false
            }
        case 'LOADING':
            return {
                ...currentState,
                loading: true
            }
        default:
            return currentState;
    }
}