import React from 'react';
import {MoviesInfo} from './container/MoviesInfo';
import { Route, Switch } from 'react-router-dom';
import { MovieInfo } from './component/MovieInfo'
import { Navbar } from './component/Navbar';
import {Footer} from './component/Footer'
import './App.css';
class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <div id='app'>
        <Navbar id='navbar' />
        <Switch>
          <Route exact path="/Movies-Series-Info/" component={MoviesInfo}></Route>
          <Route path='/MovieInfo/:ID' component={MovieInfo}></Route>
          <Route render={() => <h1>Wrong Url</h1>} />
        </Switch>
        <Footer/>
      </div>
    )
  }
}
export default App;
