import { API } from '../utils/apiKey';
import { FETCH_MOVIE } from './type'
import { instance } from '../utils/config';
export const fetchMovieAction = (movieID) => {
    let url = `?apikey=${API.apiKey}&i=${movieID}`;
    return dispatch => {
        instance.get(url).then((response) => {
            dispatch({
                payLoad : response.data,
                type : FETCH_MOVIE    
            })
            console.log(response.data);
        }).catch((reject) => {
            console.log('Rejected due to:- ', reject);
        })
    }
}