import { instance } from '../utils/config';
import { SEARCH_MOVIE } from './type';
import { API } from '../utils/apiKey';
export const searchAction = (inputValue) => {
    let url = `?apikey=${API.apiKey}&s=${inputValue}`;
    return dispatch => {
        instance.get(url).then((response) => {
            if (response.data.Response === 'True') {
                dispatch({
                    payLoad: {
                        fetchMovies: response.data.Search, searchValue: inputValue, status: true
                    },
                    type: SEARCH_MOVIE
                })
            }
            else {
                dispatch({
                    payLoad: {
                        fetchMovies: [{}], searchValue: inputValue, status: false
                    },
                    type: SEARCH_MOVIE
                })
            }

        }).catch((reject) => {
            console.log('Error:- ', reject);
        })
    }
}
