import React from 'react';
import { Input } from '../component/Input';
import { MovieCard } from '../component/Movie';
import { searchAction } from '../actions/searchAction';
import { store } from '../models/store';
import {connect} from 'react-redux';
import { Spinner } from '../component/Spinner';
import {loadingAction} from '../actions/loadingAction';
export class Movies extends React.Component {
  constructor(props) {
    super(props);
    this.searchObj = {};
    this.state = {};
  }
  takeInput(event, key) {
    this.searchObj[key] = event.target.value;
  }
  takeOperation() {
    // loadingAction();
    store.dispatch(loadingAction())
    let actionFn = searchAction(this.searchObj.text);
    store.dispatch(actionFn);
  }
  render() {
    // const {loading} = this.props;
    return (
      <>
        <Input takeOperation={this.takeOperation.bind(this)} takeInput={this.takeInput.bind(this)} />
        <div className="container">
          {this.props.loading?<Spinner/> :<MovieCard/>}
        </div>
     </>
    )
  }
}


const mapStateToProps = (state)=>{
return{
  loading :state.loading
}
}

var featuredFn = connect(mapStateToProps);
export const MoviesInfo = featuredFn(Movies);



