import React from 'react';
import { connect } from 'react-redux';
import { About } from './About';
import { fetchMovieAction } from '../actions/fetchMovieAction';
import { loadingAction } from '../actions/loadingAction';
import { Spinner } from '../component/Spinner';
import { store } from '../models/store';
import '../../node_modules/bootstrap/dist/css/bootstrap.min.css';
import './movieInfoStyle.css';

class helperMovieInfo extends React.Component {
    componentDidMount() {
        store.dispatch(loadingAction());
        let actionFn = fetchMovieAction(this.props.match.params.ID);
        store.dispatch(actionFn);
    }
    render() {
        let movieContent = (
            <div className='container'>
                <div id='moviesInfo'>
                    <div className='row' key={this.props.movieInfo.imdbID}>

                        <div className="col imgOfMovie">
                            <img src={this.props.movieInfo.Poster} alt='not found' />
                        </div>

                        <div className="col disc">
                            <h2>{this.props.movieInfo.Title}</h2>
                            <p>Released : <span>{this.props.movieInfo.Released}</span></p>
                            <p>Genre : <span>{this.props.movieInfo.Genre}</span></p>
                            <p>Rated : <span>{this.props.movieInfo.imdbRating}</span></p>
                            <p>Director : <span>{this.props.movieInfo.Director}</span></p>
                            <p>Writer : <span>{this.props.movieInfo.Writer}</span></p>
                            <p>Actor : <span>{this.props.movieInfo.Actors}</span></p>
                            <p>Language : <span>{this.props.movieInfo.Language}</span></p>
                            <p>Box Office : <span>{this.props.movieInfo.BoxOffice}</span></p>
                        </div>
                    </div>
                </div>
                <About />
            </div>)
        let content = this.props.loading ? <Spinner /> : movieContent;
        return <div>{content}</div>;
    }
}

const mapStateToProps = (state) => {
    return {
        movieInfo: state.movieInfoObj,
        loading: state.loading
    }
}
const featuredFn = connect(mapStateToProps);
export const MovieInfo = featuredFn(helperMovieInfo);
