import React from 'react';
import {Link } from 'react-router-dom';
import './aboutStyle.css';
import '../../node_modules/bootstrap/dist/css/bootstrap.min.css';
import { connect } from 'react-redux';
const helperAbout = (props) => {
    return (
        <div id="about">
            <h1>Quick Story</h1>
            <p>{props.movieInfo.Plot}</p>
            <Link to='/Movies-Series-Info/' className='link btn btn-primary'>Go Back To Search</Link>
        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        movieInfo: state.movieInfoObj
    }
}
const featuredFn = connect(mapStateToProps);
export const About = featuredFn(helperAbout);